*{
    font-family: Helvetica, sans-serif;
}

body{
    padding: 60px;
    background-color: #21232a;
    color: beige;
}

input{
    color: #21232a;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    font-size: 1.2em;
    width: 100%;
}

.card-grid{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.card{
    align-items: center;
    border: 1px solid #1f1;
    border-radius: 6px;
    margin: 0 10px 10px 0;
    overflow: hidden;
    width: 30%;
}

.card img{
    height: 90%;
    width: 100%;
}

.card p{
    color: #ccc;
    font-weight: bold;
    height: 10%;
    line-height: 30%;
    text-align: center;
    width:100%;
}

